import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import registerService from '../services/registerService';
import './Register.css';

const Register = () => {
  const [formData, setFormData] = useState({
    email: '',
    dni: '',
    name: '',
    city: '',
    phone: ''
  });
  const [errors, setErrors] = useState({});
  const [successMessage, setSuccessMessage] = useState('');
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const dniRegex = /^\d{10}$/;
    const phoneRegex = /^\d{10}$/;

    const newErrors = {};

    // Validating email
    if (!formData.email || !emailRegex.test(formData.email)) {
      newErrors.email = 'Por favor, ingrese una dirección de correo electrónico válida.';
    }

    // Validating dni
    if (!formData.dni || !dniRegex.test(formData.dni)) {
      newErrors.dni = 'Por favor, ingrese una cédula válida (10 dígitos).';
    }

    // Validating phone
    if (!formData.phone || !phoneRegex.test(formData.phone)) {
      newErrors.phone = 'Por favor, ingrese un número de teléfono válido (10 dígitos).';
    }

    if (Object.keys(newErrors).length === 0) {
      try {
        await registerService.register(formData);
        setSuccessMessage('¡Registro exitoso! Ahora puede iniciar sesión.');
        setFormData({
          email: '',
          dni: '',
          name: '',
          city: '',
          phone: ''
        });
      } catch (err) {
        setErrors(err);
      }
    } else {
      setErrors(newErrors);
    }
  };

  const handleLoginRedirect = () => {
    navigate('/login');
  };

  return (
    <div className="external-container d-flex justify-content-center align-items-center vh-100">
      <div className="container d-flex justify-content-center align-items-center flex-column">
        <div className="col-12 m-0 p-0">
          <div className="col-12 d-flex justify-content-center align-items-center">
            <img src="./logo.png" className="container-logo" alt="logo" />
          </div>
        </div>
        <div className="col-12 m-0 p-0 mt-3 mb-3">
          <div className="d-flex justify-content-center align-items-center flex-column">
            <div className="col-12 col-lg-6 d-flex justify-content-center align-items-center flex-column">
              <div className="register-container">
                {successMessage ? (
                  <div>
                    <p className="m-0 p-0">{successMessage}</p>
                    <button onClick={handleLoginRedirect}>Ir a Iniciar Sesión</button>
                  </div>
                ) : (
                  <form className="register-form" onSubmit={handleSubmit}>
                    <div className="form-group">
                      <input
                        type="text"
                        className="input-register-form"
                        name="name"
                        placeholder="Nombre"
                        value={formData.name}
                        onChange={handleChange}
                        required
                      />
                      {errors.name && <p className="error-message m-0 p-0 text-white Bebas-Neue">{errors.name}</p>}
                    </div>
                    <div className="form-group">
                      <input
                        type="text"
                        className="input-register-form"
                        name="dni"
                        placeholder="Cédula"
                        value={formData.dni}
                        onChange={handleChange}
                        required
                      />
                      {errors.dni && <p className="error-message m-0 p-0 text-white Bebas-Neue">{errors.dni}</p>}
                    </div>
                    <div className="form-group">
                      <input
                        type="text"
                        className="input-register-form"
                        name="phone"
                        placeholder="Teléfono"
                        value={formData.phone}
                        onChange={handleChange}
                        required
                      />
                      {errors.phone && <p className="error-message m-0 p-0 text-white Bebas-Neue">{errors.phone}</p>}
                    </div>
                    <div className="form-group">
                      <input
                        type="email"
                        className="input-register-form"
                        name="email"
                        placeholder="E-mail"
                        value={formData.email}
                        onChange={handleChange}
                        required
                      />
                      {errors.email && <p className="error-message m-0 p-0 text-white Bebas-Neue">{errors.email}</p>}
                    </div>
                    <div className="form-group">
                      <input
                        type="text"
                        className="input-register-form"
                        name="city"
                        placeholder="Ciudad"
                        value={formData.city}
                        onChange={handleChange}
                        required
                      />
                      {errors.city && <p className="error-message m-0 p-0 text-white Bebas-Neue">{errors.city}</p>}
                    </div>
                    <div className="row m-0 p-0 Bebas-Neue d-flex justify-content-center align-items-center">
                      <p className="m-0 p-0 mt-3 mb-3 container-text-legals">
                        Al hacer clic en "Registrarse", aceptas nuestras
                        <span><a href="" className="text-decoration-none color-redirect-blue">Condiciones</a></span>
                        y la <span><a href="" className="text-decoration-none color-redirect-blue">Política de privacidad</a></span>. 
                        Es posible que te enviemos notificaciones por SMS, que puedes desactivar cuando quieras.
                      </p>
                    </div>
                    <div className="d-flex justify-content-center align-items-center">
                      <button type="submit" className="btn-register-two w-50 Bebas-Neue">Registrarse</button>
                    </div>
                  </form>
                )}
                {errors.non_field_errors && <p className="error-message m-0 p-0 text-white Bebas-Neue">{errors.non_field_errors}</p>}
                <Link to="/" className="back-home-link Bebas-Neue text-decoration-none text-redirect-login">Volver al inicio</Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;
